import React, { useState } from 'react';
//antd imports
import { Form, Input, Button, Row, Col, Spin, message, Popconfirm } from 'antd';
import { FormOutlined, CameraOutlined } from '@ant-design/icons';
import { PlusOutlined, CloseOutlined } from '@ant-design/icons';
import axios from 'axios';
//
const EditProject = (props) => {
  const [dataChanged, setDataChanged] = useState(false);
  const [spin, setSpin] = useState(false);
  const [form] = Form.useForm();
  const token = localStorage.getItem('token');
  const uid = localStorage.getItem('uid');
  const data = props.data;

  const onFinish = (values) => {
    const updatedData = {
      email: data.email,
      creatorsID: uid,
      projectTitle: values.projectTitle,
      projectSubTitle: values.projectSubTitle,
      teamName: values.teamName,
      desc: values.desc,
      projectLink: values.projectLink,
      abstractLink: values.abstractLink,
      status: 'pending',
    };
    setSpin(true);
    axios
      .put(
        `https://ginnovation-server.herokuapp.com/api/project/${data._id}`,
        updatedData,
        {
          headers: { Authorization: `Bearer ${token}` },
        }
      )
      .then((resp) => {
        setSpin(false);
        message.success('Successfully Edited Data');
        props.handleClose(true);
        props.refresh();
      })
      .catch((err) => {
        setSpin(false);
        message.error('An error occured!');
      });
  };
  const deleteProject = () => {
    setSpin(true);
    axios
      .delete(
        `https://ginnovation-server.herokuapp.com/api/project/${data._id}`,
        {
          headers: { Authorization: `Bearer ${token}` },
        }
      )
      .then((resp) => {
        setSpin(false);
        if (resp.data.status === 'success') {
          message.success('Successfully Deleted Data');
          props.handleClose(true);
          props.refresh();
        } else {
          message.error('Oops! Something went wrong');
        }
      })
      .catch((err) => {
        setSpin(false);
        message.error('An error occured!');
      });
  };
  return (
    <div className='modal-container'>
      {spin && (
        <div className='modal-container'>
          <Spin size='large' />
        </div>
      )}
      <div className='edit-modal'>
        <h4 className='head'>
          Edit Project <PlusOutlined className='icon' />
          <CloseOutlined
            className='close-btn'
            onClick={() => {
              props.handleClose(dataChanged);
            }}
          />
        </h4>
        <Form
          form={form}
          name='addBlog'
          onFinish={onFinish}
          initialValues={data}
        >
          <Row gutter={8}>
            <Col md={12}>
              <Form.Item
                name='projectTitle'
                rules={[
                  {
                    required: true,
                    message: 'Please input the title!',
                  },
                ]}
              >
                <Input placeholder='Enter Project Title' showCount />
              </Form.Item>
            </Col>
            <Col md={12}>
              <Form.Item
                name='projectSubTitle'
                rules={[
                  {
                    required: true,
                    message: 'Please input the subtitle!',
                  },
                ]}
              >
                <Input placeholder='Enter Project Sub-Title' showCount />
              </Form.Item>
            </Col>
            <Col md={8}>
              <Form.Item
                name='projectLink'
                rules={[
                  {
                    required: true,
                    message: 'Please input the link!',
                  },
                ]}
              >
                <Input placeholder='Enter Project Link' />
              </Form.Item>
            </Col>
            <Col md={8}>
              <Form.Item
                name='abstractLink'
                rules={[
                  {
                    required: true,
                    message: 'Please input the abstarct link!',
                  },
                ]}
              >
                <Input placeholder='Enter Abstract Link' />
              </Form.Item>
            </Col>
            <Col md={8}>
              <Form.Item
                name='teamName'
                rules={[
                  {
                    required: true,
                    message: 'Please input the teamname!',
                  },
                ]}
              >
                <Input placeholder='Enter Team name' />
              </Form.Item>
            </Col>
            <Col md={24}>
              <Form.Item
                name='desc'
                rules={[
                  {
                    required: true,
                    message: 'Please input the description!',
                  },
                ]}
              >
                <Input.TextArea placeholder='Enter About Your Project' />
              </Form.Item>
            </Col>
          </Row>
          <div className='form-btn-row'>
            <div className='register-btn-container btn-container mt-3'>
              <Button
                type='secondary'
                className='mr-2'
                onClick={props.handleClose}
              >
                Cancel
              </Button>
              <Popconfirm
                placement='bottomRight'
                title='Are you sure to delete this task?'
                onConfirm={deleteProject}
              >
                <Button type='danger' className='mr-2'>
                  Delete
                </Button>
              </Popconfirm>
              <Button type='primary' htmlType='submit'>
                Save Changes
              </Button>
            </div>
          </div>
        </Form>
      </div>
    </div>
  );
};

export default EditProject;
